import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SchoolYearTermComponent } from './school-year-term.component';

const routes: Routes = [{
  path: '',
  component: SchoolYearTermComponent,
  data: { postMessage: false }
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SchoolYearTermRoutingModule { }
